const URL = '';
// DEBUGmock
const DEBUG = true;
const LOADINGTEXT = '...';
// 
const AUTH = 'LINJINGHUI';

// 
let ajaxGetGoods = (pms, callback) => {
	let params = {
		page: pms.page || 1,
		size: pms.size || 10
	};
	
	uni.showLoading({title: LOADINGTEXT});
	uni.request({
		url: URL + '/goods',
		method: 'GET',
		data: params,
		header: {
			'AUTH': AUTH
		},
		dataType: 'json',
		complete: (data) => {
			uni.hideLoading();
		},
		fail: () => {
			uni.showToast({title: '', icon: 'none', position: 'bottom'});
		},
		success: (data) => {
			callback && callback(data);
		}
	});
}

if (DEBUG) {
	const majax = require('./mock.js');
	ajaxGetGoods = majax.ajaxGetGoods;
}

export {ajaxGetGoods};
